function testWatchUpdates(){
    const data = toProxy({
        name:'a',
        friend:{
            name:'b',
            fav:{
                food:"c"
            }
        }
    })
    console.log('data =', data);
    watchUpdates(data, (info)=>{
        console.log('testWatchUpdates_fn watchUpdates', info)
    })
    data.name = 'c'
    data.friend.name = 'd'
    data.friend.fav.food = 'e'
}
